
.app-modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.55);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99999;
  backdrop-filter: blur(3px);
}

.app-modal-window {
  background: #1e1e1e;
  border-radius: 12px;
  padding: 25px;
  width: 420px;
  animation: appmodal 0.25s ease-out;
  border: 1px solid #444;
  box-shadow: 0 0 20px rgba(0,0,0,0.35);
}

@keyframes appmodal {
  from { transform: scale(0.85); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

.app-modal-window input,
.app-modal-window select,
.app-modal-window textarea {
  width: 100%;
  padding: 10px;
  background: #2b2b2b;
  border: 1px solid #555;
  border-radius: 6px;
  color: white;
  margin-bottom: 12px;
}

.app-modal-window button {
  width: 100%;
  padding: 12px;
  background: #008cff;
  border: none;
  border-radius: 6px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  margin-top: 8px;
}

.app-modal-window button.secondary {
  background: #555;
}
